<template>
  <div>
    <h3>service</h3>
    <el-row :gutter="20">
      <el-col :span="5">
        
        <el-input class="input-package" placeholder="service" :disabled="!projectSetting.createService" v-model="serviceSetting.servicePackage"/>
      </el-col>
      <el-col :span="16">
        <el-col :span="8">
          <el-checkbox label="" :disabled="!projectSetting.createService" v-model="serviceSetting.createInterface"/>
        </el-col>
        <el-col :span="8">
          <el-checkbox label="Service" :disabled="!projectSetting.createService||projectSetting.mybatisType===1" v-model="serviceSetting.useBaseService"/>
        </el-col>
        <el-col :span="8">
          <el-checkbox label="" :disabled="!projectSetting.createService" v-model="serviceSetting.useTransactional"/>
        </el-col>
      </el-col>
    </el-row>


  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  export default {
    name: "Project",
    computed: {
      ...mapGetters(["serviceSetting","projectSetting"])
    }
  }
</script>

<style scoped>
  @import "../../../static/css/content.css";
  .input-package{
    width: 52%;
  }
</style>



